"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { CodeBlock } from "@/components/code-block"; // Zak≈Çadamy, ≈ºe CodeBlock istnieje
import { ExplanationSection } from "@/components/explanation-section";

// Uproszczona definicja typu dla SolutionTabsProps
interface SolutionTabsProps {
    // W SolutionTabsProps musi byƒá mo≈ºliwo≈õƒá przyjƒôcia undefined, poniewa≈º
    // w SolutionPageClient.tsx (stan ≈Çadowania) jest na poczƒÖtku undefined.
    explanation?: { 
        answer?: string; 
        error?: string; 
        code?: string;
        language?: string;
    };
}

// üõë Zmieniamy wym√≥g na opcjonalny "?" w typowaniu, aby uniknƒÖƒá b≈Çƒôd√≥w
export function SolutionTabs({ explanation }: SolutionTabsProps) {
    const [activeTab, setActiveTab] = useState<"explanation" | "solution">("explanation");
    
    // Wydobywamy kod i jƒôzyk (musimy polegaƒá na tym, ≈ºe sƒÖ w explanation)
    const code = explanation?.code || '≈Åadowanie kodu...';
    const language = explanation?.language || 'python'; // Domy≈õlny jƒôzyk, gdy dane siƒô ≈ÇadujƒÖ

    return (
        <div className="mb-8">
            <div className="flex gap-2 mb-6 border-b">
                <Button
                    variant={activeTab === "explanation" ? "default" : "ghost"}
                    onClick={() => setActiveTab("explanation")}
                    className="rounded-b-none"
                >
                    Wyja≈õnienie
                </Button>
                <Button
                    variant={activeTab === "solution" ? "default" : "ghost"}
                    onClick={() => setActiveTab("solution")}
                    className="rounded-b-none"
                >
                    RozwiƒÖzanie
                </Button>
            </div>

            {activeTab === "explanation" ? (
                // PRZEKAZUJEMY explanation (mo≈ºe byƒá undefined, obs≈Çu≈ºone w ExplanationSection)
                <ExplanationSection explanation={explanation} />
            ) : (
                <div className="rounded-lg border bg-card p-6">
                    {/* Przekazujemy domy≈õlne/za≈Çadowane warto≈õci do CodeBlock */}
                    <CodeBlock code={code} language={language} /> 
                </div>
            )}
        </div>
    );
}